import React, { useEffect, useState } from 'react';
import { Toast, ToastContainer } from 'react-bootstrap';
import { racineUrl } from '../database/DataApi';

const Toastify = ({ showToast }) => {
    const [show, setShow] = useState(showToast);

    useEffect(() => {
        setShow(showToast);
    }, [showToast]);

    return (
        <ToastContainer position='bottom'>
            <Toast data-aos="fade-up" bg='success' onClose={() => setShow(false)} show={show} delay={5000} autohide>
                <Toast.Header>
                    <img
                        style={{ width: '10%'}}
                        src={racineUrl + "/logoD.jpg"}
                        className="rounded me-2"
                        alt="logo"
                    />
                    <strong className="me-auto">DagDevelop</strong>
                    <small>1 mins ago</small>
                </Toast.Header>
                <Toast.Body>E-mail envoyé !</Toast.Body>
            </Toast>
        </ToastContainer>
    );
};

export default Toastify;