import { collection, doc, getDocs, updateDoc, query, where, deleteDoc } from 'firebase/firestore';
import { db } from './firebase.config';


export const racineUrl = `${process.env.PUBLIC_URL}/assets/images`;
export const euro = "€";

const collectionRef = collection(db, "formations");
const codePromosRef = collection(db, "codePromo");



export const getFormations = async () => {
    const data = await getDocs(collectionRef);
    return data.docs.map((doc) => ({ ...doc.data(), id: doc.id }));
    
}

export const updateFormation = async (id, nbPlacesRestante) => {
    const docRef = doc(db, "formations", id);
    const data = {
        nbPlacesRestante
    }

    await updateDoc(docRef, data);
}


export const codePromoExist = async (code) => {
    
    const q = query (codePromosRef, where("libellé", "==", code));
    const data = await getDocs(q);
    const datas = data.docs.map((doc) => ({ ...doc.data(), id: doc.id }));
    
    const codePromo = datas.find(c => c.libellé === code);
    return codePromo;
}

export const deleteCodePromo = async (id) => {
    await deleteDoc(doc(db, "codePromo", id));
}





