import React, { useCallback, useState } from 'react';
import { PayPalButtons, usePayPalScriptReducer } from '@paypal/react-paypal-js';
import '../style/Paypal.scss';
import { Spinner } from 'react-bootstrap';
import { deleteCodePromo, updateFormation } from '../database/DataApi';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
//import emailJs from '@emailjs/browser';

/**
 * 
 * isResolved
 * isRejected
 * isInitial
 */


const MyPaypal = ({ formation, horaireJour, setShow, setHoraireJour, setHoraireSoir, codePromo, handleCloseModal }) => {

    const [{ isPending }] = usePayPalScriptReducer();
    const [infoProduit, setInfoProduit] = useState(`${formation.titre} (${horaireJour})`);
    const [jour, setJour] = useState(horaireJour);
    const [code, setCode] = useState(codePromo);
    const [codeToDelete, setCodeToDelete] = useState(false);
    const navigate = useNavigate();

    const updateData = useCallback(({ id, nbPlacesRestante }) => {
        jour ? --nbPlacesRestante.journée : --nbPlacesRestante.soirée;
        updateFormation(id, nbPlacesRestante).then(() => {
            setShow(false);
            navigate("/");

            setHoraireJour(true);
            setHoraireSoir(false);
        });

    }, [navigate, jour, setHoraireJour, setHoraireSoir, setShow]);

    let deleteCode = useCallback(({ id }) => {

        deleteCodePromo(id).then(() => {
            updateData(formation);
        }).catch(err => console.log(err));
    }, [updateData, formation]);


   
    useEffect(() => {

        setJour(horaireJour);
        setCode(codePromo);
        
        if (codeToDelete) {
            deleteCode(codePromo);
            setCodeToDelete(false);
        }

        horaireJour ? setInfoProduit(`${formation.titre} (Journée)`) : setInfoProduit(`${formation.titre} (Soirée)`);

    }, [horaireJour, formation.titre, codePromo, codeToDelete, deleteCode]);


    
    return (
        <div className='paypalBtn'>
            {!isPending ? (
                <PayPalButtons
                    createOrder={(data, actions) => {

                        return actions.order.create({
                            purchase_units: [
                                {
                                    amount: {
                                        value: formation.prix,
                                    },
                                    description: infoProduit
                                },
                            ]
                        });
                    }}

                    onApprove={async (data, actions) => {
                        console.log(actions);
                        console.log(data);
                        //const details = await actions.order.capture();
                        //const name = details.payer.name.given_name;
                        //rediriger vers une page de remerciement puis vers le home !

                        /*const info = {
                            description: infoProduit,
                            value : formation.prix
                        }*/

                        //envoyer un mail de confirmation
                        //emailJs.send(process.env.REACT_APP_SERVICE_SUCCESS_PAY_ID, process.env.REACT_APP_TEMPLATE_SUCCESS_PAY_ID, info, process.env.REACT_APP_PUBLIC_KEY_EMAIL)

                        setCodeToDelete(true);
                    }}

                    onCancel={async (data, actions) => {
                        //alert(`Dommage que tu aies annulé la transaction :( !`);
                        //console.log("code in onCancel : ", code);
                    
                    }}
                    onError={async (err) => {
                        //vers la page d'érreur
                        console.log("erreur : ", err);
                    }}

                    style={{ layout: "vertical", color: "gold", shape: "pill" }} />
            ) : (

                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            )}

        </div>
    );
};

export default MyPaypal;