

export const applyCodeToPrice = (codePromo, oldPrice) => {
    const discountStr = codePromo.substr(codePromo.length - 2, codePromo.length);
    const discount = Number(discountStr);

    if (isNaN(discount)){
        return oldPrice;
    }
        
    return Math.round(oldPrice - ((oldPrice * discount) / 100));

}