import React, { useEffect, useRef, useState } from 'react';
import { Card, Container, Form, Modal, FloatingLabel, Col, Row, Stack } from 'react-bootstrap';
import '../style/MyCard.scss'
import '../style/CarousselItem.scss';
import { euro, codePromoExist } from '../database/DataApi';
import MyPaypal from './MyPaypal';
import { applyCodeToPrice } from '../util/util';
import { useCallback } from 'react';

const MyCard = (props) => {
    const { image, title, text, price } = props;
    const [currentFormation, setCurrentFormation] = useState(props.currentFormation);
    const [show, showModal] = useState(false);
    const codePromo = useRef();
    const [codePromoAPI, setCodePromoAPI] = useState({});
    const [horaireJour, setHoraireJour] = useState(true);
    const [horaireSoir, setHoraireSoir] = useState(false);
    const [ancienPrix, setAncienPrix] = useState(currentFormation.prix);
    const [hidden, setHidden] = useState('hidden');
    const [hiddenCode, setHiddenCode] = useState('hidden');
    const nbPlaces = currentFormation.nbPlacesRestante.journée + currentFormation.nbPlacesRestante.soirée;


    useEffect(() => {
        setCurrentFormation(props.currentFormation);

    }, [props]);

    const setShow = useCallback((value) => {
        showModal(value)
    }, []);

    const handleCloseModal = () => {
        setShow(false);
        hidden === 'visible' && setHidden('hidden');
        hiddenCode === 'visible' && setHiddenCode('hidden');
        currentFormation.prix !== ancienPrix && (currentFormation.prix = ancienPrix);
    }

    const handleChangeCode = () => hiddenCode === 'visible' && setHiddenCode('hidden');


    const applyCodePromo = () => {

        if (hidden === 'hidden') {
            codePromoExist(codePromo.current.value).then((c) => {
                setCodePromoAPI(c);

                if (c !== undefined) {

                    setHidden('visible');
                    hiddenCode === 'visible' && setHiddenCode('hidden');
                    setAncienPrix(currentFormation.prix);
                    currentFormation.prix = applyCodeToPrice(codePromo.current.value, ancienPrix);
                    codePromo.current.value = "";
                } else
                    setHiddenCode('visible');

            }).catch(() => {
                setHiddenCode('visible');
            });
        } else {
            codePromo.current.value = "";
        }
    }


    return (
        <Container>
            <Card className='card' >
                <Card.Img variant="top" src={image} />
                <Card.Body>
                    <Card.Title as="h3">{title}</Card.Title>
                    <h6>
                        <Card.Text>{text}</Card.Text>
                    </h6>
                    <Card.Title className='mb-3'> <strong>{price}</strong> </Card.Title>
                    <button disabled={nbPlaces <= 0} onClick={() => setShow(true)} className={nbPlaces > 0 ? 'button' : 'buttonDisabled'}>Détails</button>
                    <Card.Text>
                        {
                            nbPlaces > 0 ? (
                                <em style={nbPlaces > 10 ? { color: 'green' } : { color: 'red' }}>
                                    {nbPlaces} places disponibles !
                                </em>
                            ) : (
                                <em style={{ color: 'red' }}>
                                    Complet !
                                </em>
                            )
                        }
                    </Card.Text>
                </Card.Body>
            </Card>

            {/************  DETAILS *************/}

            {/* Modifier le modal design et mettre l'option pour code réduction */}

            <Modal show={show} fullscreen={true} onHide={() => handleCloseModal()}>
                <Modal.Header closeButton>
                    <Modal.Title>Acheter la formation</Modal.Title>
                </Modal.Header>
                <Modal.Body className="mt-4">

                    <div className="mod">
                        <div className="details" id="titre">
                            <h2> {currentFormation.titre} </h2>
                        </div>
                        <div className="details" id="description">
                            <div>
                                <h4>{currentFormation.description}</h4>
                            </div>
                        </div>

                        <hr />
                        <div className="details" >
                            <h2 style={{ visibility: `${hidden}`, textDecoration: "line-through", color: "red" }} > {ancienPrix} {euro} </h2>
                            <h2 id="prix"> <strong>{currentFormation.prix} {euro}</strong></h2>
                        </div>
                        <div className="details codePromo">
                            <Row>
                                <Col md={6} lg={6}>
                                    <FloatingLabel
                                        label="Code promo"
                                        onChange={() => handleChangeCode()}
                                    >
                                        <Form.Control id="code" ref={codePromo} required type="text" placeholder="Code promo" />
                                        <span style={{ visibility: `${hiddenCode}`, color: "red" }}><em>Code promo invalide !</em></span>
                                    </FloatingLabel>
                                </Col>
                                <Col md={5} lg={6} onClick={() => applyCodePromo()} className="myBtn">
                                    Appliquer
                                </Col>
                            </Row>
                        </div>
                        <hr />

                        <Stack className='details mb-4'>
                            <div>
                                <img className='imageFormation' src={currentFormation.image} alt="formation" />
                            </div>
                        </Stack>
                        <Stack>
                            <div style={{ textAlign: 'left' }} className='p-2'>
                                <ul>
                                    <li>Date de début : <span><strong> {currentFormation.dateDebut} </strong></span></li>
                                    <li>Durée : <strong>{currentFormation.durée}</strong></li>
                                    <li>Formateur : {currentFormation.formateur}</li>
                                </ul>
                            </div>
                            <button className='button'>Table de matière</button>
                        </Stack>
                    </div>

                    <h2 style={{ textAlign: 'center' }} className="details">Choisir l'horaire (<em >Tous les dimanches</em>) :</h2>
                    <div className="horaire">
                        <div className="details">
                            <Form.Check
                                disabled={currentFormation.nbPlacesRestante.journée > 0 ? false : true}
                                defaultChecked={currentFormation.nbPlacesRestante.journée > 0 ? true : false}
                                type={'radio'}
                                label={`De 10h00 à 14h00`}
                                name='jour'
                                onClick={() => {
                                    setHoraireJour(true);
                                    setHoraireSoir(false);
                                }}
                            />
                            <em
                                style={currentFormation.nbPlacesRestante.journée > 5 ? { color: 'green' } : { color: 'red' }}>
                                ({currentFormation.nbPlacesRestante.journée} places disponibles)
                            </em>
                        </div>
                        <div className="details">
                            <Form.Check
                                disabled={currentFormation.nbPlacesRestante.soirée > 0 ? false : true}
                                type={'radio'}
                                label={`De 20h00 à 23h59`}
                                name='jour'
                                onClick={() => {
                                    setHoraireSoir(true);
                                    setHoraireJour(false);
                                }}
                            />
                            <em
                                style={currentFormation.nbPlacesRestante.soirée > 5 ? { color: 'green' } : { color: 'red' }}>
                                ({currentFormation.nbPlacesRestante.soirée} places disponibles)
                            </em>
                        </div>
                    </div>
                    <hr />

                    <MyPaypal codePromo={codePromoAPI}  setShow={setShow} formation={currentFormation} setHoraireJour={setHoraireJour} setHoraireSoir={setHoraireSoir} horaireJour={horaireJour} />
                </Modal.Body>
            </Modal>
        </Container>
    );
};

export default MyCard;

//100 €