import React, { useState, useEffect, useRef } from 'react';
import { FloatingLabel, Form } from 'react-bootstrap';
import '../style/FormContact.scss';
import '../style/CarousselItem.scss';
import AOS from 'aos';
import emailJs from '@emailjs/browser';
import Toastify from './Toast';


const FormContact = () => {
    const [validated, setValidated] = useState(false);
    const name = useRef("");
    const email = useRef("");
    const gsm = useRef("");
    const motivation = useRef("");
    const [showToast, setShowToast] = useState(false);

    useEffect(() => {
        AOS.refresh();
        showToast && setShowToast(false);
    }, [showToast]);

    const handleSubmit = (event) => {
        const form = event.currentTarget;
        event.preventDefault();

        if (form.checkValidity() === false) {
            event.stopPropagation();
        }else{
            const data = {
                name: name.current.value,
                email: email.current.value,
                gsm: gsm.current.value,
                motivation: motivation.current.value
            };

            emailJs.send(process.env.REACT_APP_SERVICE_ID, process.env.REACT_APP_TEMPLATE_ID, data, process.env.REACT_APP_PUBLIC_KEY_EMAIL)
            .then((res) => {
                setShowToast(true);
                setValidated(false);

                name.current.value = "";
                email.current.value = "";
                gsm.current.value = "";
                motivation.current.value = "";

            }).catch((err) => {
                console.log("Failled...", err);
                
            });
        }

        setValidated(true);
    };


    return (
        <div className='form mb-4'>
            <Form data-aos="fade-up" noValidate validated={validated} onSubmit={handleSubmit}>
                <FloatingLabel

                    data-aos="slide-right"
                    controlId="floatingInput"
                    label="Nom et Prénom*"
                    className="mb-5 formControl"
                >
                    <Form.Control ref={name} required type="text" placeholder="Vially Dag" />
                    <Form.Control.Feedback as="p" type='invalid'>veillez saisir votre nom et prénom !</Form.Control.Feedback>
                </FloatingLabel>

                <FloatingLabel
                    data-aos="slide-left"
                    controlId="floatingInput"
                    label="Adresse e-mail*"
                    className="mb-5 formControl"
                >

                    <Form.Control ref={email} required type="email" placeholder="name@example.com" />
                    <Form.Control.Feedback as="p" type='invalid'>veillez saisir une adresse e-mail valide !</Form.Control.Feedback>
                </FloatingLabel>

                <FloatingLabel
                    data-aos="slide-right"
                    controlId="floatingInput"
                    label="Numéro de téléphone*"
                    className="mb-5 formControl"
                >
                    <Form.Control ref={gsm} required type="text" placeholder="+32492473290" />
                    <Form.Control.Feedback as="p" type='invalid'>numéro de téléphone obligatoire !</Form.Control.Feedback>
                </FloatingLabel>
                <FloatingLabel
                    data-aos="slide-left"
                    controlId="floatingInput"
                    label="Motivation*"
                    className="mb-4 formControl"
                >
                    <Form.Control
                        ref={motivation}
                        required
                        as="textarea"
                        placeholder="Tes motivations"
                        style={{ height: '100px', resize: 'none' }}
                    />
                    <Form.Control.Feedback as="p" type='invalid'>Ce champ est obligatoire !</Form.Control.Feedback>
                </FloatingLabel>
                <div data-aos="slide-right"><button type="submit" className="button largeBtn" >postuler</button></div>
            </Form>
            <Toastify showToast={showToast} />
        </div>
    );
};

export default FormContact;